@import './reset.css';

body,
button {
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: 500;
}

/* Media query --- mobile */
@media only screen and (min-width: 320px) and (max-width: 768px) {
    html {
        font-size: 14px;
    }
}
/* Media query --- tablet */
@media only screen and (min-width: 768px) and (max-width: 1024px) {
    html {
        font-size: 16px;
    }
}
